.figure {
  width: 84px;
  min-height: 131px;
  position: absolute;
}

.figure:hover {
  cursor: pointer;
}

.figure_invisible {
  visibility: hidden;
}
.figure_visible {
  visibility: visible;
}

.figure__good {
  background-image: url(../../images/goodboss.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.figure__bad {
  background-image: url(../../images/badboss.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.figure__disappeared {
  opacity: 0;
  transform: translateY(-800px);
  transition: all 0.8s ease-in;
}

.figure__explode {
  opacity: 0;
  transform: scale(0);
  transition: all 0.5s ease-in;
}

.figure__bubble {
  width: 170px;
  height: 170px;
  position: absolute;
  visibility: hidden;
}

.figure__bubble_visible {
  visibility: visible;
  opacity: 0;
  transform: translateY(-800px);
  transition: all 0.8s ease-in;
}

.figure__bubble-fault_visible {
  visibility: visible;
  opacity: 0;
  transform: scale(0);
  transition: all 0.5s ease-in;
}