.popup {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0,0,0,0.5);
  z-index: 10;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s linear 0.2s, opacity 0.2s linear;
}

.popup_opened {
  opacity: 1;
  visibility: visible;
  transition-delay: 0s;
}

.popup__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 334px;
  height: 582px;
  box-sizing: border-box;
  background-color: transparent;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  background-image: url(../../images/start_popup.png);
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat; 
}
.popup__image {
  height: 147px;
  width: 147px;
  margin: 18px 0 auto;
}
.popup__button {
  width: 279px;
  height: 89px;
  margin: auto 0 56px;
  padding: 0;
  box-sizing: border-box;
  border: none;
  background-color: transparent;
  background-image: url(../../images/button_start.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  transition: all 0.2s linear;
}

.popup__button:hover {
  cursor: pointer;
  opacity: 0.8;
  transform: scale(0.95);
  transition: all 0.1s linear;
  
}
